.link {
  color: inherit;
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

:before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  list-style: none;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

button {
  cursor: pointer;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

:root {
  --accent-font-family: "Titan One";
  --second-font-family: "DM Sans", sans-serif;
  --accent-text-color: #d41443;
  --background-accent-button-color: #d41342;
  --middle-accent-text-color: #ffa5ba;
  --main-text-color: #fff;
  --description-title-color: #000;
  --description-text-color: #907e82;
  --background-main-color: #ffb8ca;
  --background-middle-color: #fff5f6;
  --border-down-color: #e1e1e1;
  --social-icon-color: #fea5bb;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.big-button {
  text-align: center;
  min-width: 161px;
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  padding: 10px 17px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.big-button--primary {
  color: var(--main-text-color);
  background-color: var(--background-accent-button-color);
  margin-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
}

.big-button--primary .big-buttom__icon {
  fill: var(--main-text-color);
}

.big-button--secondary {
  color: var(--accent-text-color);
  background-color: #fff;
  margin-left: auto;
  margin-right: auto;
}

.big-button--secondary .big-buttom__icon {
  fill: var(--accent-text-color);
}

.big-buttom__icon {
  margin-left: 8px;
}

.hero {
  background-color: var(--background-main-color);
  overflow: hidden;
}

.hero-titel {
  font-family: var(--accent-font-family);
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--main-text-color);
  margin-bottom: 20px;
  font-size: 26px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.15;
}

.hero-titel__word {
  font-family: var(--accent-font-family);
  text-transform: uppercase;
  color: var(--accent-text-color);
  font-size: 48px;
  font-style: normal;
  font-weight: 400;
  line-height: .96;
}

.hero-btn {
  width: 62px;
  height: 24px;
  background-color: var(--accent-text-color);
  text-align: center;
  color: #fff;
  border-radius: 20px;
  margin-right: 8px;
  padding-top: 7px;
  font-family: DM Sans;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
  text-decoration: none;
  display: inline-block;
}

.secondary {
  background-color: var(--main-text-color);
  color: var(--accent-text-color);
  width: 83px;
}

.hero-container {
  z-index: 5;
  padding-top: 136px;
  padding-bottom: 158px;
  position: relative;
}

.circle {
  width: 346px;
  height: 344px;
  background: var(--middle-accent-text-color);
  z-index: -2;
  border-radius: 50%;
  position: absolute;
  top: 25px;
  left: 36px;
}

.hero-block__ice {
  z-index: -1;
  position: absolute;
  top: 34px;
  left: 119px;
}

.hero-block__list, .fish, .hero-block__milk, .hero-block__girl {
  display: none;
}

.header {
  background-color: #ffb8ca;
  padding-top: 8px;
  padding-bottom: 8px;
}

.header__container {
  align-items: center;
  display: flex;
}

.backdrop {
  width: 100vw;
  height: 100%;
  background-color: rgba(95, 93, 93, .286);
  transition: opacity .3s, visibility .3s;
  position: fixed;
  top: 0;
  overflow-y: scroll;
}

.menu__open-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  margin-left: auto;
  display: block;
}

.menu__open-icon {
  stroke: var(--accent-text-color);
  transition: stroke 250ms var(--timing-function);
}

.menu__open-icon:hover, .menu__open-icon:focus {
  stroke: var(--main-text-color);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 10;
  transition: transform 250ms var(--timing-function);
  background-color: #e17992;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  position: fixed;
  top: 0;
  right: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu__close-btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
  display: block;
  position: absolute;
  top: 20px;
  right: 20px;
}

.menu__close-icon {
  stroke: var(--main-text-color);
  transition: stroke 250ms var(--timing-function);
}

.menu__close-icon:hover, .menu__close-icon:focus {
  stroke: var(--accent-text-color);
}

.menu__btn {
  width: 168px;
  height: 44px;
  background-color: var(--main-text-color);
  color: var(--accent-text-color);
  font-family: var(--second-font-family);
  letter-spacing: .03px;
  transition: background-color 250ms var(--timing-function);
  border: none;
  border-radius: 22px;
  outline: none;
  justify-content: center;
  align-items: baseline;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: flex;
}

.menu__btn:after {
  content: "";
  width: 6px;
  height: 8px;
  background-image: url("button-arrow.38e819aa.svg");
  background-size: contain;
  margin-left: 28px;
}

.menu__btn:hover, .menu__btn:focus {
  background-color: #ecebeb;
}

.menu__item {
  border-top: 1px solid #e18298;
}

.menu__item:last-child {
  border-bottom: 1px solid #e18298;
}

.menu__link {
  font-family: var(--second-font-family);
  letter-spacing: .03px;
  color: var(--main-text-color);
  transition: color 250ms var(--timing-function);
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.28;
  display: block;
}

.menu__link:hover, .menu__link:focus {
  color: var(--accent-text-color);
}

