}

.products__button:hover .products__arrow, .products__button:focus .products__arrow {
  fill: #d41443;
}

.products__item {
  position: relative;
}

.products__animation {
  height: calc(100% - 72px);
  width: 200px;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: transform .35s linear;
  display: flex;
  position: absolute;
  top: 0;
  transform: translateX(-200%);
}

.products__button:hover + .products__animation, .products__button:focus + .products__animation {
  transform: translate(0);
}

